























import { Vue, Component } from "vue-property-decorator";

import { Navigation } from "../utils/navigation";

export interface NavigationItem {
  icon: string;
  title: string;
  link: Navigation;
}

@Component
export default class TheNavigationCorner extends Vue {
  navigationItems: NavigationItem[] = [
    {
      icon: "mdi-karate",
      title: "",
      link: Navigation.TaeKwonDo
    },
    {
      icon: "mdi-foot-print",
      title: "",
      link: Navigation.Association
    },
    // {
    //   icon: "mdi-account-group",
    //   title: ""
    // },
    {
      icon: "mdi-school",
      title: "",
      link: Navigation.Course
    },
    {
      icon: "mdi-phone",
      title: "",
      link: Navigation.ContactUs
    }
  ];

  /* Methods */
  onNavItemClicked(item: NavigationItem) {
    this.$emit("navigate", item.link);
  }
}
