



























































import { Vue, Component, Ref, Watch } from "vue-property-decorator";
import { getModule } from "vuex-module-decorators";
import layoutStore from "@/store/layout.store";

@Component
export default class Taekwondo extends Vue {
  @Ref("tkdWrapper") readonly tkdWrapper!: HTMLDivElement;

  layoutModule = getModule(layoutStore, this.$store);

  checkOnScroll = true;
  messages = [
    {
      label: "",
      isVisible: false
    },
    {
      label: "",
      isVisible: false
    },
    {
      label: "",
      isVisible: false
    },
    {
      label: "",
      isVisible: false
    },
    {
      label: "",
      isVisible: false
    }
  ];

  /* Watchers */
  @Watch("scrollPosition")
  onScroll(pos: number) {
    if (!this.checkOnScroll) return;
    if (pos > this.wrapperTop - 400) this.messages[0].isVisible = true;
    if (pos > this.wrapperTop - 300) this.messages[1].isVisible = true;
    if (pos > this.wrapperTop - 200) this.messages[2].isVisible = true;
    if (pos > this.wrapperTop - 100) this.messages[3].isVisible = true;
    if (pos > this.wrapperTop - 50) {
      this.messages[4].isVisible = true;
      this.checkOnScroll = false;
    }
  }

  /* Computed Properties */
  get scrollPosition() {
    return this.layoutModule.scrollPosition;
  }

  get wrapperTop() {
    return this.tkdWrapper.offsetTop;
  }
}
