


























import { Vue, Component } from "vue-property-decorator";
import { getModule } from "vuex-module-decorators";
import layoutStore from "@/store/layout.store";

import TheAppBar from "@/components/TheAppBar.vue";
import TheNavigationCorner from "@/components/TheNavigationCorner.vue";
import Landing from "@/views/Landing.vue";
import Course from "@/views/Course.vue";
import Taekwondo from "@/views/Taekwondo.vue";
import Association from "@/views/Association.vue";
import ContactUs from "@/views/ContactUs.vue";
import { Navigation } from "@/utils/navigation";

@Component({
  components: {
    TheAppBar,
    TheNavigationCorner,
    Landing,
    Course,
    Taekwondo,
    Association,
    ContactUs
  }
})
export default class AppLayout extends Vue {
  layoutModule = getModule(layoutStore, this.$store);

  /* Hooks */
  created() {
    window.addEventListener("scroll", this.onScroll);
  }

  /* Methods */
  onScroll() {
    const scrollPosition = window.pageYOffset;
    this.layoutModule.setScrollPosition(scrollPosition);
  }

  onNavigate(link: Navigation) {
    let scrollTo: number | undefined = 0;
    switch (link) {
      case Navigation.TaeKwonDo:
        scrollTo = document.getElementById("Taekwondo")?.offsetTop;
        break;
      case Navigation.Association:
        scrollTo = document.getElementById("Association")?.offsetTop;
        break;
      case Navigation.Course:
        scrollTo = document.getElementById("Course")?.offsetTop;
        break;
      case Navigation.ContactUs:
        scrollTo = document.getElementById("ContactUs")?.offsetTop;
        break;
      default:
        break;
    }
    if (!scrollTo) return;
    window.scrollTo({
      top: scrollTo,
      behavior: "smooth"
    });
  }
}
