


























import { Vue, Component, Ref } from "vue-property-decorator";
import gsap from "gsap";

import TheNavigationCorner from "@/components/TheNavigationCorner.vue";
import { Navigation } from "../utils/navigation";

@Component({
  components: {
    TheNavigationCorner
  }
})
export default class Landing extends Vue {
  isLandingCenterHovered = false;
  landingCenterText = "Active";

  @Ref("landing-center-text")
  readonly landingCenterTextElement!: HTMLSpanElement;

  /* Methods */
  onBottomNavClicked() {
    this.onNavigate(Navigation.Course);
  }

  onLandingCenterHovered(isHovered: boolean) {
    if (isHovered) {
      gsap
        .to(this.landingCenterTextElement, {
          marginLeft: "-600px",
          duration: 0.5
        })
        .then(() => {
          this.landingCenterText = "";
          gsap.to(this.landingCenterTextElement, {
            marginLeft: "0",
            duration: 0.5
          });
        });
    } else {
      gsap
        .to(this.landingCenterTextElement, {
          marginLeft: "-600px",
          duration: 0.5
        })
        .then(() => {
          this.landingCenterText = "Active";
          gsap.to(this.landingCenterTextElement, {
            marginLeft: "0",
            duration: 0.5
          });
        });
    }
  }

  onNavigate(link: Navigation) {
    this.$emit("navigate", link);
  }
}
